import BlogList from "./BlogList";
import usePosts from "../hooks/usePosts";
import Loading from "./Loading";

const Home = () => {
  const { postlist, isPending, error } = usePosts("all");

  return (
    <div className="home">
      {error && <div>{error}</div>}
      {isPending && <Loading />}
      {!isPending && <BlogList blogs={postlist} view="home" />}
    </div>
  );
};

export default Home;
