import { useParams } from "react-router";
import "../css/blogdetails.css";
import CommentBox from "./CommentBox";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState, useEffect } from "react";
import * as FirestoreService from "../services/firestore";
import { convertFromRaw, Editor, EditorState } from "draft-js";
import altimg from "../img/plane.jpg";
import { convertDateToString } from "../services/shared";
import Loading from "./Loading";

const BlogDetails = () => {
  const { id } = useParams();
  const [isPending, setIsPending] = useState(true);

  const [editorState, setEditorState] = useState(null);
  const [title, setTitle] = useState(null);
  const [name, setName] = useState(null);
  const [tags, setTags] = useState(null);
  const [imgurl, setImgURL] = useState(null);

  const [created, setCreated] = useState(null);

  useEffect(() => {
    FirestoreService.getPost(id).then((result) => {
      const contentState = convertFromRaw(result.data()["post"]);
      const data = EditorState.createWithContent(contentState);
      FirestoreService.updateViews(id);

      setEditorState(data);
      setTitle(result.data()["title"]);
      setName(result.data()["name"]);
      setTags(result.data()["tags"]);
      setImgURL(result.data()["imgurl"]);
      setCreated(convertDateToString(result.data()["created"].toDate()));
      setIsPending(false);

      console.log(`${result.data()["views"]} views`);
    });
  }, [id]);

  return (
    <div className="blog-details">
      {isPending && <Loading />}
      {!isPending && (
        <div className="blogdetails">
          <article>
            <img
              className="detailsimg"
              src={imgurl}
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = altimg;
              }}
              alt=""
            />

            <h2 className="blogtitle">{title}</h2>
            <div className="basicinfo">
              <div className="col left">
                <FontAwesomeIcon className="faicon" icon="user" />
                <p></p>
                <FontAwesomeIcon className="faicon" icon="calendar-day" />
                <p></p>
                <FontAwesomeIcon className="faicon" icon="tag" />
              </div>
              <div className="col right">
                <p>{name}</p>
                <p>{created}</p>
                <div className="tags">
                  {tags.map((tag) => (
                    <a
                      href={`/blogs/tags/${tag}`}
                      className="tag"
                      id={tag}
                      key={tag}
                    >
                      #{tag} &nbsp;
                    </a>
                  ))}
                </div>
              </div>
            </div>
            {editorState != null && (
              <Editor editorState={editorState} readOnly={true} />
            )}
          </article>
          <CommentBox postid={id} />
        </div>
      )}
    </div>
  );
};

export default BlogDetails;
