import { Link } from "react-router-dom";
import "../css/blogcard.css";
import altimg from "../img/plane.jpg";

const BlogCard = ({ img, title, summary, id, type }) => {
  return (
    <Link to={`/blogs/${id}`}>
      <div className={`blogcard ${type}`}>
        <img
          className="cardimg"
          src={img}
          onError={(e) => {
            e.target.onerror = null;
            e.target.src = altimg;
          }}
          alt=""
        />
        <div className="caption">
          <h1 className="cardtitle">{title}</h1>
          <p className="cardsummary">{summary}</p>
        </div>
      </div>
    </Link>
  );
};

export default BlogCard;
