import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useState } from "react";
import "../css/navbar.css";
import { auth } from "../services/firestore";

const Navbar = ({ user }) => {
  const [toggle, setToggle] = useState(false);

  const handleCloseToggle = () => {
    setToggle(!toggle);
    document.querySelector(".mobile-navlinks").style.width = "0";
    document.body.style.overflow = "auto";
  };
  const handleOpenToggle = () => {
    setToggle(!toggle);
    document.body.style.overflow = "hidden";
    document.querySelector(".mobile-navlinks").style.width = "70vw";
  };

  const handleSignOut = () => {
    auth
      .signOut()
      .then(() => {
        console.log("signed out");
        handleCloseToggle();
      })
      .catch((err) => console.log("error signing out", err));
  };

  return (
    <div>
      <nav className="navbar">
        <Link to="/" className="logo">
          <h1>Pludose</h1>
        </Link>

        {/* navlinks */}
        <ul className="navlinks">
          <Link to="/">Home</Link>
          <Link to="/about">About</Link>
          <Link to="/blogs/tags/all">All Posts</Link>
          {user ? (
            <Link to="/create">Create</Link>
          ) : (
            <Link to="/auth">Sign In/Sign Up</Link>
          )}

          {user && <Link onClick={handleSignOut}>Log Out</Link>}
        </ul>

        <div className="navicons">
          <FontAwesomeIcon
            icon="bars"
            onClick={handleOpenToggle}
            className="mobile-menu openbtn"
          />
        </div>
      </nav>

      {/* mobile-navlinks */}
      <div className="mobilewrapper">
        <div className="transparentdiv"></div>
        <div className="mobile-navlinks">
          <FontAwesomeIcon
            icon="times"
            onClick={handleCloseToggle}
            className="mobile-menu closebtn"
          />

          <Link to="/" onClick={handleCloseToggle}>
            Home
          </Link>

          <Link to="/blogs/tags/all" onClick={handleCloseToggle}>
            All Posts
          </Link>
          <Link to="/about" onClick={handleCloseToggle}>
            About
          </Link>
          {user ? (
            <div className="authlinks">
              <Link to="/create" onClick={handleCloseToggle}>
                Create
              </Link>
              <Link to="/" id="logoutbtn" onClick={handleSignOut}>
                Logout
              </Link>
            </div>
          ) : (
            <div className="notauthlinks">
              <Link to="/auth" onClick={handleCloseToggle}>
                Sign In/Sign Up
              </Link>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Navbar;
