import BlogCard from "./BlogCard";
import "../css/blogcard.css";

const BlogList = ({ blogs, view }) => {
  if (view === "home") {
    return (
      <div className="blog-list">
        {blogs.map((blog, i) => (
          <BlogCard
            img={blog.imgurl}
            title={blog.title}
            summary={blog.summary}
            id={blog.id}
            key={blog.id}
            type={i % 2 === 0 ? "even" : "odd"}
          />
        ))}
      </div>
    );
  } else {
    return (
      <div className="blog-list">
        {blogs.map((blog) => (
          <BlogCard
            img={blog.imgurl}
            title={blog.title}
            summary={blog.summary}
            id={blog.id}
            key={blog.id}
            type="catcard"
          />
        ))}
      </div>
    );
  }
};

export default BlogList;
