import BlogList from "./BlogList";
import { useParams } from "react-router";
import { useState } from "react";
import { useEffect } from "react";
import * as FirestoreService from "../services/firestore";
import "../css/categories.css";
import useTags from "../hooks/useTags";

const Categories = () => {
  const { tag } = useParams();

  //get list of tags
  const tagList = useTags();

  const [mytag, setMytag] = useState(tag);
  const [sort, setSort] = useState("newest");
  const [postlist, setPostlist] = useState([]);
  const [isPending, setIsPending] = useState(true);

  //every time chosentag changes, new api call
  useEffect(() => {
    setPostlist([]);
    FirestoreService.getPosts(mytag, sort).onSnapshot((querySnapshot) => {
      querySnapshot.forEach((doc) => {
        setPostlist((postlist) => [
          ...postlist,
          {
            title: doc.data()["title"],
            name: doc.data()["name"],
            imgurl: doc.data()["imgurl"],
            summary: doc.data()["summary"],
            id: doc.id,
          },
        ]);
      });
    });
    setIsPending(false);
  }, [mytag, sort]);

  const handleChange = (e) => {
    setMytag(e.currentTarget.value);
  };

  return (
    <div className="categories">
      {isPending && <div>Loading...</div>}

      <div className="mobilecategories">
        <label htmlFor="sort">Sort by:</label>
        <div className="sort">
          <select
            name="sort"
            id="sort"
            onChange={(e) => {
              setSort(e.target.value);
            }}
          >
            <option value="newest">newest</option>
            <option value="popularity">popularity</option>
            <option value="oldest">oldest</option>
          </select>
        </div>
        <br />
        <label htmlFor="tags">Tags:</label>
        <div className="tags">
          <select name="tags" id="tags" value={mytag} onChange={handleChange}>
            {tagList.map((tagitem) => (
              <option value={tagitem.tag} key={tagitem.id}>
                {tagitem.tag}
              </option>
            ))}
          </select>
        </div>
      </div>

      <BlogList blogs={postlist} view="categories" />
    </div>
  );
};

export default Categories;
