import {
  Editor,
  EditorState,
  RichUtils,
  ContentState,
  AtomicBlockUtils,
} from "draft-js";
import "draft-js/dist/Draft.css";
import { useEffect, useState } from "react";
import { convertToRaw } from "draft-js";
import * as FirestoreService from "../services/firestore";
import "../css/create.css";
import useTags from "../hooks/useTags";

const MyEditor = ({ user }) => {
  const [editorState, setEditorState] = useState(() =>
    EditorState.createEmpty()
  );
  const [title, setTitle] = useState("");
  const [name, setName] = useState("");
  const [summary, setSummary] = useState("");
  const [imgurl, setImgUrl] = useState("");

  const [tags, setTags] = useState([]);
  const [myTags, setMyTags] = useState([]);

  const [msg, setMsg] = useState();
  const [complete, setComplete] = useState(false);
  const tagList = useTags();

  const handleTagClick = (e) => {
    const name = e.target.innerHTML;
    if (e.target.classList.contains("selected")) {
      e.target.classList.remove("selected");
      setTags(tags.filter((item) => item !== name));
    } else {
      e.target.classList.add("selected");
      setTags((tags) => [...tags, name]);
    }
  };

  const handleSubmit = () => {
    setTags((tags) => [...tags, ...myTags]);
    console.log(tags);
    //convert to raw js object tobestored in fb
    const data = convertToRaw(editorState.getCurrentContent());

    //create post with firebase
    FirestoreService.createPost(data, title, name, tags, summary, imgurl)
      .then(() => {
        setEditorState(
          EditorState.push(
            editorState,
            ContentState.createFromText(""),
            "remove-range"
          )
        );
        setMsg("Post successfully written!");
        setTitle("");
        setName("");
        setTags("");

        var x = document.querySelector("#snackbar");
        x.className = "show";
        setTimeout(function () {
          x.className = x.className.replace("show", "");
        }, 3000);
      })
      .catch((error) => {
        setMsg("Error creating post");
        console.error("Error writing document: ", error);

        var x = document.querySelector("#snackbar");
        x.className = "show";
        setTimeout(function () {
          x.className = x.className.replace("show", "");
        }, 3000);
      });
  };

  //set complete to true (enable button) when all fields are filled in
  useEffect(() => {
    if (
      title.length + name.length + tags.length >= 2 &&
      editorState.getCurrentContent().hasText()
    ) {
      setComplete(true);
    } else {
      setComplete(false);
    }
  }, [title, name, tags, editorState]);

  //style buttons
  const handleKeyCommand = (command, editorState) => {
    const newState = RichUtils.handleKeyCommand(editorState, command);
    if (newState) {
      setEditorState(newState);
      return "handled";
    } else {
      return "not-handled";
    }
  };
  const _onClick = (e) => {
    setEditorState(RichUtils.toggleInlineStyle(editorState, e.target.name));
  };
  const styles = ["BOLD", "ITALIC", "UNDERLINE"];
  const buttons = styles.map((style) => {
    return (
      <button key={style} onClick={_onClick} name={style}>
        {style}
      </button>
    );
  });

  return user ? (
    user.admin ? (
      <div className="create">
        <h2>Create New Post</h2>

        <div className="postinfo">
          <input
            placeholder="title"
            type="text"
            id="title"
            value={title}
            onChange={(e) => setTitle(e.currentTarget.value)}
          />
          <input
            placeholder="name"
            type="text"
            id="name"
            value={name}
            onChange={(e) => setName(e.currentTarget.value)}
          />
          <input
            placeholder="img url"
            type="text"
            id="imgurl"
            value={imgurl}
            onChange={(e) => setImgUrl(e.currentTarget.value)}
          />
          <input
            placeholder="summary"
            type="text"
            id="summary"
            value={summary}
            onChange={(e) => setSummary(e.currentTarget.value)}
          />
          <input
            type="text"
            placeholder="add new tags separated by commas (or choose from existing below)"
            onChange={(e) => {
              setMyTags((myTags) => [...myTags, ...e.target.value.split(",")]);
            }}
          />
          <div className="tagopts">
            {tagList.map((tag) => (
              <div className="tagopt" key={tag.id} onClick={handleTagClick}>
                {tag.tag}
              </div>
            ))}
          </div>
        </div>
        <div className="toolbar">{buttons}</div>
        <Editor
          editorState={editorState}
          onChange={setEditorState}
          handleKeyCommand={handleKeyCommand}
          placeholder="Write New Post"
        />

        <button
          disabled={complete ? false : true}
          id="createsubmit"
          onClick={handleSubmit}
        >
          Submit
        </button>
        <div id="snackbar">{msg}</div>
      </div>
    ) : (
      <p className="redirect message">
        Thank you for signing up! Are you interested in contributing to this
        blog? Reach us at pludose@gmail.com.
      </p>
    )
  ) : (
    <p className="redirect message">You are not authorized to view this page</p>
  );
};
export default MyEditor;
