export const convertDateToString = (datetime) => {
  var date = datetime.toISOString().split("T")[0];
  var hours = datetime.getHours();
  var minutes = datetime.getMinutes();
  var ending = hours >= 12 ? "pm" : "am";
  var displayedhours = hours >= 12 ? hours - 12 : hours;
  return `${date} ${displayedhours}:${minutes} ${ending}`;
};
