//getuser
export const getCurrentUser = (uid) => {
  return db.collection("users").doc(uid).get();
};

// POSTS
//get posts for search/bloglist (filtered/all)
export const getPosts = (tag, sort) => {
  const tmpref =
    tag === "all"
      ? db.collection("posts")
      : db.collection("posts").where("tags", "array-contains", tag);
  if (sort === "newest") {
    return tmpref.orderBy("created", "desc");
  } else if (sort === "oldest") {
    return tmpref.orderBy("created", "asc");
  } else if (sort === "popularity") {
    return tmpref.orderBy("views", "asc");
  }
};

//get specific post details
export const getPost = (postid) => {
  return db.collection("posts").doc(postid).get();
};

//create post
export const createPost = (postobj, title, name, tags, summary, imgurl) => {
  return db.collection("posts").doc().set({
    name: name,
    title: title,
    imgurl: imgurl,
    summary: summary,
    post: postobj,
    tags: tags,
    created: firebase.firestore.Timestamp.now(),
    views: 0,
  });
};

export const updateViews = (postid) => {
  return db
    .collection("posts")
    .doc(postid)
    .update({ views: firebase.firestore.FieldValue.increment(1) });
};

//COMMENTS
//create comment
export const createComment = (comment, name, postid, replyid = 0) => {
  return db.collection("comments").doc().set({
    comment: comment,
    name: name,
    likes: 0,
    replyid: replyid,
    postid: postid,
    created: firebase.firestore.Timestamp.now(),
  });
};

export const getComments = (tag, sort) => {
  const cmtref = db.collection("comments");
  return cmtref.orderBy("created", "desc");
};

export const getTags = () => {
  return db.collection("tags").get();
};
